import React from 'react';
import $ from 'jquery';
import Module from './../../Module';
import './RocketPassModule.css';

class RocketPassModule extends Module {

    static width = 2;
    static height = 1;

    constructor(props) {
        super(props);
        this.state = {
            date: new Date(),
            start: new Date('2019-08-28 17:00:00Z'),
            finish: new Date('2019-12-03 17:00:00Z')
        };
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick() {
        console.log('foo');
    }

    render() {
        let progress = (this.state.date - this.state.start)/(this.state.finish - this.state.start);
        return (
            <div id="module">
                <svg width="100%" viewBox="0 0 100 50">
                    <image x="2" y="11" height="30" xlinkHref="./Modules/RocketPassModule/rp4_logo.png" onClick={this.handleClick}/>
                    <text x="75" y="13" dominantBaseline="middle" textAnchor="middle" fontSize="18" fontWeight="bold" fill="white">{Math.ceil(this.props.props['targetLevel']*progress)}</text>
                    <line x1="60" y1="25" x2="90" y2="25" style={({stroke:"white", strokeWidth:2})} />
                    <text x="75" y="37" dominantBaseline="middle" textAnchor="middle" fontSize="18" fontWeight="bold" fill="white">{this.props.props['targetLevel']}</text>
                </svg>
            </div>
        );
    }
}

export default RocketPassModule;
