import React from 'react';
import Module from './../../Module';
import './ClockAnalogModule.css';

class ClockAnalogModule extends Module {

    static width = 2;
    static height = 2;

    constructor(props) {
        super(props);
        this.state = {date: new Date()};
        this.handleClick = this.handleClick.bind(this);
    }

    componentDidMount() {
        this.timerID = setInterval(() => this.tick(),1000);
    }

    componentWillUnmount() {
        clearInterval(this.timerID);
    }

    tick() {
        this.setState({date: new Date()});
    }

    handleClick() {
    }

    render() {
        let second = this.state.date.getSeconds();
        let minute = this.state.date.getMinutes()+second/60.0;
        let hour = this.state.date.getHours()+minute/60.0;
        let hourHand = {};
        let minuteHand = {};
        let secondHand = {};
        hourHand.x = Math.cos((hour%12)/12.0*2*Math.PI-Math.PI/2);
        hourHand.y = Math.sin((hour%12)/12.0*2*Math.PI-Math.PI/2);
        minuteHand.x = Math.cos((minute)/60.0*2*Math.PI-Math.PI/2);
        minuteHand.y = Math.sin((minute)/60.0*2*Math.PI-Math.PI/2);
        secondHand.x = Math.cos((second)/60.0*2*Math.PI-Math.PI/2);
        secondHand.y = Math.sin((second)/60.0*2*Math.PI-Math.PI/2);

        return (
            <div id="module" onClick={this.handleClick}>
                <svg width="100%" viewBox="0 0 50 50">
                    <line className="clockDigitalModuleHour" x1="25" y1="25" x2={hourHand.x*15+25} y2={hourHand.y*15+25} strokeLinecap="round"/>
                    <line className="clockDigitalModuleMinute" x1="25" y1="25" x2={minuteHand.x*22+25} y2={minuteHand.y*22+25} strokeLinecap="round"/>
                    <line className="clockDigitalModuleSecond" x1="25" y1="25" x2={secondHand.x*25+25} y2={secondHand.y*25+25} strokeLinecap="round"/>
                </svg>
            </div>
        );
    }
}

export default ClockAnalogModule;
