import React from 'react';
import Module from './../../Module';
import './CountDownModule.css';

class CountDownModule extends Module {

    static width = 2;
    static height = 1;

    constructor(props) {
        super(props);
        this.state = {
            timeLeft: 'Loading'
        };
        this.handleClick = this.handleClick.bind(this);
    }

    componentDidMount() {
        this.timerID = setInterval(() => this.tick(),1000);
        this.updateTimeLeft();
    }

    componentWillUnmount() {
        clearInterval(this.timerID);
    }

    tick() {
        this.updateTimeLeft();
    }

    handleClick() {
    }

    padNumber(number) {
        if(number < 10) {
            return "0" + number;
        }
        return number;
    }

    updateTimeLeft() {
        let timeLeft = '';
        let now = new Date();
        let diffMs = this.props.props.date - now;
        let diffDays = Math.ceil(diffMs / 1000 / 60 / 60 / 24);
        if(diffDays > 0) {
            if(diffDays >= 7) {
                let diffWeeks = Math.ceil(diffDays/7);
                timeLeft = diffWeeks + (diffWeeks>1?' weeks':' week');
            } else {
                timeLeft = diffDays + (diffDays>1?' days':' day');
            }
        } else if(diffMs >= 0) {
            let diffH = Math.floor(diffMs / 1000 / 60 / 60);
            let minutes = Math.floor((diffMs / 1000 / 60)%60);
            let seconds = Math.ceil((diffMs / 1000)%60);
            timeLeft = this.padNumber(diffH) + ':' + this.padNumber(minutes) + ':' + this.padNumber(seconds);
        } else {
            timeLeft= '00:00:00';
        }
        this.setState({timeLeft: timeLeft});
    }

    render() {
        return (
            <div id="module" onClick={this.handleClick}>
                <div>
                    <svg width="100%" viewBox="0 0 100 50">
                        <text x="50" y="8" dominantBaseline="middle" textAnchor="middle" fontSize="10" fontWeight="bold" fill="white">{this.props.props.title}</text>
                        <text x="50" y="32" dominantBaseline="middle" textAnchor="middle" fontSize="15" fill="white">{this.state.timeLeft}</text>
                    </svg>
                </div>
            </div>
        );
    }
}

export default CountDownModule;
