import React from 'react';
import './css/Grid.css';
import Module from './Module';
import ClockDigitalModule from "./modules/ClockDigitalModule/ClockDigitalModule";
import ClockAnalogModule from "./modules/ClockAnalogModule/ClockAnalogModule";
import F1NextRaceModule from "./modules/F1NextRaceModule/F1NextRaceModule";
import RocketPassModule from "./modules/RocketPassModule/RocketPassModule";
import CountDownModule from "./modules/CountDownModule/CountDownModule";
import NfsCountDownModule from "./modules/NfsCountDownModule/NfsCountDownModule";

class Grid extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            width: 8,
            height: 8,
            modules: []
        };

        this.handleClick = this.handleClick.bind(this);
    }

    componentDidMount() {
        // this.addModule(Module, 0,0);
        this.addModule(ClockDigitalModule, 3,2);
        this.addModule(ClockAnalogModule, 3,0);
        this.addModule(F1NextRaceModule, 1,0);
        this.addModule(RocketPassModule, 1,1, {targetLevel: 250});
        // this.addModule(CountDownModule, 0,3, {title: 'End of Semester', date: new Date('2019-12-13 14:00:00Z')});
        this.addModule(NfsCountDownModule, 5,0, {date: new Date('2019-11-08 18:00:00Z')});
    }

    addModule(moduleType, x, y, props) {
        let module = {width: moduleType.width, height: moduleType.height, x: x, y: y, props: props};
        module.module = React.createElement(moduleType, module);

        this.setState((state) => ({
            modules: [...state.modules, module]
        }), () => this.forceUpdate());


    }


    handleClick() {
        this.forceUpdate();
    }

    render() {
        let grid = [];
        for (let i = 0; i < this.state.width; i++) {
            grid[i] = [];
        }

        this.state.modules.forEach((module, index) => {
            for(let x = module.x; x < module.x + module.width; x++) {
               for(let y = module.y; y < module.y + module.height; y++) {
                    grid[x][y] = true;
               }
           }
           grid[module.x][module.y] = module;
        });

        let rows = [];
        for (let y = 0; y < this.state.height; y++) {
            let row = [];
            for (let x = 0; x < this.state.width; x++) {
                if(typeof grid[x][y] === 'object') {
                    row.push(<td colSpan={grid[x][y].width} rowSpan={grid[x][y].height}>
                            {grid[x][y].module}
                        </td>)
                } else if (grid[x][y] === undefined){
                    row.push(<td/>)
                }
            }
            rows.push(<tr>{row}</tr>)
        }
        return (
            <div>
                {/*<div id="topBar" onClick={this.handleClick}/>*/}
                <div id="grid">
                    <table>
                        <tbody>
                            {rows}
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}

export default Grid;
