import React from 'react';
import $ from 'jquery';
import Module from './../../Module';
import './F1NextRaceModule.css';

class F1NextRaceModule extends Module {

    static width = 2;
    static height = 1;

    constructor(props) {
        super(props);
        this.state = {
            date: null,
            timeLeft: 'Loading',
            raceName: '',
            raceLocation: '',
        };
        this.handleClick = this.handleClick.bind(this);
    }

    componentDidMount() {
        this.timerID = setInterval(() => this.tick(),1000);
        this.updateNextRaceDate();
    }

    componentWillUnmount() {
        clearInterval(this.timerID);
    }

    tick() {
        this.updateTimeLeft();
    }

    handleClick() {
    }

    padNumber(number) {
        if(number < 10) {
            return "0" + number;
        }
        return number;
    }

    updateNextRaceDate() {
        $.getJSON("http://ergast.com/api/f1/current/next.json", (data) => {
            let dateStr = data['MRData']['RaceTable']['Races'][0]['date'];
            let timeStr = data['MRData']['RaceTable']['Races'][0]['time'];
            let dateTime = new Date(dateStr + ' ' + timeStr);
            let raceName = data['MRData']['RaceTable']['Races'][0]['raceName'];
            let location = data['MRData']['RaceTable']['Races'][0]['Circuit']['Location'];
            let raceLocation = location['locality'] + ', ' + location['country'];
            this.setState({date: dateTime, raceName: raceName, raceLocation: raceLocation}, this.updateTimeLeft);
        });
    }

    updateTimeLeft() {
        let timeLeft = '';
        let now = new Date();
        let diffMs = this.state.date - now;
        let diffDays = Math.ceil(diffMs / 1000 / 60 / 60 / 24);
        if(diffDays > 0) {
            timeLeft = diffDays + (diffDays>1?' days':' day');
        } else if(diffDays >= 0) {
            let diffH = Math.floor(diffMs / 1000 / 60 / 60);
            let minutes = Math.floor((diffMs / 1000 / 60)%60);
            let seconds = Math.ceil((diffMs / 1000)%60);
            timeLeft = this.padNumber(diffH) + ':' + this.padNumber(minutes) + ':' + this.padNumber(seconds);
        } else {
            timeLeft= 'Started';
        }

        this.setState({timeLeft: timeLeft});
    }

    render() {


        return (
            <div id="module" onClick={this.handleClick}>
                <div>
                    <svg width="100%" viewBox="0 0 100 50">
                        <text x="50" y="8" dominantBaseline="middle" textAnchor="middle" fontSize="10" fontWeight="bold" fill="white">{this.state.raceName}</text>
                        <text x="50" y="17" dominantBaseline="middle" textAnchor="middle" fontSize="8" fill="white">{this.state.raceLocation}</text>
                        <image x="2" y="25" height="10" xlinkHref="https://www.formula1.com/etc/designs/fom-website/images/f1_logo.svg"/>
                        <text x="70" y="32" dominantBaseline="middle" textAnchor="middle" fontSize="15" fill="white">{this.state.timeLeft}</text>
                    </svg>
                </div>
            </div>
        );
    }
}

export default F1NextRaceModule;
