import React from 'react';
import Module from './../../Module';
import './ClockDigitalModule.css';

class ClockDigitalModule extends Module {

    static width = 2;
    static height = 1;

    constructor(props) {
        super(props);
        this.state = {date: new Date()};
        this.handleClick = this.handleClick.bind(this);
    }

    componentDidMount() {
        this.timerID = setInterval(() => this.tick(),1000);
    }

    componentWillUnmount() {
        clearInterval(this.timerID);
    }

    tick() {
        this.setState({date: new Date()});
    }

    handleClick() {
    }

    render() {
        return (
            <div id="module" onClick={this.handleClick}>
                <svg width="100%" viewBox="0 0 100 50">
                  <text x="50" y="27" dominantBaseline="middle" textAnchor="middle" fontSize="25" fontFamily="Segment7Standard" fill="lime">{this.state.date.toLocaleTimeString('hu-HU').replace(/:/g, '.')}</text>
                </svg>
            </div>
        );
    }
}

export default ClockDigitalModule;
